import React from "react"
import "./styles.scss"
import * as Path from "./lib"

export default React.memo(function Icon(props) {
  const size = props.size ?? "24"
  const category = props.category ?? "material"
  const icon = props.icon ?? "add_circle"

  return (
    <i className="icon" id={props.id} disabled={props.disabled}>
      {props.onClick && (
        <div className="icon-click-area" onClick={props.onClick} />
      )}
      <svg
        className={props.className}
        style={props.style}
        viewBox="0 0 24 24"
        width={`${size}px`}
        height={`${size}px`}
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
      >
        {Path[category][icon]}
      </svg>
    </i>
  )
})
