import React from "react"
import "./styles/index.scss"

export default function Button(props) {
  const isIconButton = React.Children.count(props.children) === 0 ? true : false
  // HTML tag definition
  const tagName = props.href ? "a" : "button"
  const { as: Component = tagName } = props
  // Props
  const className = props.className ? ` ${props.className}` : ""
  const variant = props.variant ? getVariant(props.variant) : "btn-filled-blue"
  const size = getSize(props.size, props.as, isIconButton)
  const borders = getBorders(props.className)
  const disabled = props.disabled ? "disabled" : ""
  const transparent = props.transparent ? "btn-transparent" : ""
  const block = props.block ? "btn-block" : ""
  const inputValue = props.value ?? "Input button"
  const type = props.type ?? "button"
  // Generate class list
  let classList = `btn ${size} ${borders} ${disabled} ${transparent} ${block} ${variant}${className}`

  switch (props.as) {
    case "input":
      return (
        <div
          id={props.id}
          className={classList}
          style={props.style}
          onClick={props.onClick}
        >
          {props.leftIcon}
          <input type={type} value={inputValue} />
          {props.rightIcon}
        </div>
      )
    default:
      return (
        <Component
          id={props.id}
          className={classList}
          style={props.style}
          onClick={props.onClick}
          type={props.type}
          aria-label={props["aria-label"]}
          // -> a
          href={props.href}
          target={props.target}
          role={props.role}
        >
          {props.leftIcon}
          {props.children}
          {props.rightIcon}
        </Component>
      )
  }
}

function getVariant(variant) {
  switch (variant) {
    // Filled buttons
    case "red":
      return "btn-filled-red"
    case "orange":
      return "btn-filled-orange"
    case "yellow":
      return "btn-filled-yellow"
    case "green":
      return "btn-filled-green"
    case "teal":
      return "btn-filled-teal"
    case "blue" || !variant:
      return "btn-filled-blue"
    case "indigo":
      return "btn-filled-indigo"
    case "purple":
      return "btn-filled-purple"
    case "pink":
      return "btn-filled-pink"
    case "gray":
      return "btn-filled-gray"
    // Outlined buttons
    case "outlined-red":
      return "btn-outlined-red"
    case "outlined-orange":
      return "btn-outlined-orange"
    case "outlined-yellow":
      return "btn-outlined-yellow"
    case "outlined-green":
      return "btn-outlined-green"
    case "outlined-teal":
      return "btn-outlined-teal"
    case "outlined-blue" || "outlined":
      return "btn-outlined-blue"
    case "outlined-indigo":
      return "btn-outlined-indigo"
    case "outlined-purple":
      return "btn-outlined-purple"
    case "outlined-pink":
      return "btn-outlined-pink"
    case "outlined-gray":
      return "btn-outlined-gray"
  }
}

function getSize(size, tagName, isIconButton) {
  let buttonSize
  if (size === "lg") buttonSize = "btn-large"
  if (size === "md") buttonSize = "btn-medium"
  if (size === "sm") buttonSize = "btn-small"
  if (!size) buttonSize = "btn-medium"

  if (size === "lg" && isIconButton === true && tagName !== "input")
    buttonSize = "btn-icon-large"
  if (size === "md" && isIconButton === true && tagName !== "input")
    buttonSize = " btn-icon-medium"
  if (size === "sm" && isIconButton === true && tagName !== "input")
    buttonSize = "btn-icon-small"
  if (!size && isIconButton === true && tagName !== "input")
    buttonSize = "btn-icon-medium"

  return buttonSize
}

function getBorders(className) {
  if (className === undefined || className.indexOf("rounded" !== -1)) {
    return "rounded"
  }
  return
}
